:root {
  --font-inter:"Inter", sans-serif;
}

:root {
  --limegreen:hsl(163, 72%, 41%);
  --brightred:hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter:hsl(203, 89%, 53%);
  --instagram-start:hsl(37, 97%, 70%);
  --instagram-middle:hsl(5,77%,71%);
  --instagram-end:hsl(329, 70%, 58%);
  --youtube:hsl(348, 97%, 39%);
  --toggle-bg-start:hsl(210,78%,56%);
  --toggle-bg-end:hsl(146, 68%, 55%);
  --toggle-light:hsl(230, 19%, 60%);
  --dark-bg:hsl(230, 17%, 14%);
  --dark-top-bg:hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-text1:hsl(228, 34%, 66%);
  --dark-text2:hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(230, 47%, 96%);
  --light-card-hover:hsl(228,33%,91%);
  --light-text1:hsl(228, 12%, 44%);
  --light-text2:hsl(230, 17%, 14%);
  --dark-card-hover:hsl(225,25%,27%);
  --number-c:black;
  --background:var(--light-bg);
  --text-color:var(--light-text2);
  --text-color2:var(--light-text1);
  --card-bg:var(--light-card);
  --card-hover:var(--light-card-hover);
  --number:var(--number-c);
  --toggle:var(--toggle-light);
  --button:var(--light-bg);
  --button-bg:var(--light-text1);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background:var(--dark-bg);
    --text-color:var(--dark-text2);
    --text-color2:var(--dark-text1);
    --card-bg:var(--dark-card);
    --card-hover:var(--dark-card-hover);
    --number:white;
    --toggle:var(--light-bg);
    --button:var(--dark-bg);
    --button-bg:linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
  }
}
body.light {
  --background:var(--light-bg);
  --text-color:var(--light-text2);
  --text-color2:var(--light-text1);
  --card-bg:var(--light-card);
  --card-hover:var(--light-card-hover);
  --number:var(--number-c);
  --toggle:var(--toggle-light);
  --button:var(--light-bg);
  --button-bg:var(--light-text1);
}

body.dark {
  --background:var(--dark-bg);
  --text-color:var(--dark-text2);
  --text-color2:var(--dark-text1);
  --card-bg:var(--dark-card);
  --card-hover:var(--dark-card-hover);
  --number:white;
  --toggle:var(--light-bg);
  --button:var(--dark-bg);
  --button-bg:linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--text-color);
  min-height: 150vh;
}

.visually-impaired {
  position: absolute;
  left: -1000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  margin-top: 2.25rem;
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color2);
}

.cards {
  display: grid;
  gap: 1.875rem;
  grid-template-columns: repeat(1fr);
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background: var(--card-bg);
  color: var(--dark-text1);
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  position: relative;
  overflow: hidden;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--card-hover);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.3125rem;
  background: linear-gradient(225deg, var(--instagram-start), var(--instagram-middle), var(--instagram-end));
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
  margin-top: 0.3125rem;
  height: 1.25rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-weight: bold;
  font-size: 0.75rem;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  font-weight: bold;
  color: var(--number);
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  font-size: 0.75rem;
  font-weight: 400;
  color: var(--dark-text1);
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
}
.card__change {
  font-size: 0.75rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__number {
  margin-left: 0.25rem;
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: none;
  gap: 0.3rem 0.5rem;
}
.toggle label {
  font-size: 1.125rem;
  color: var(--toggle);
}
.toggle label[for=dark] {
  line-height: 1.5rem;
}
.toggle__wrapper {
  height: 1.5rem;
  grid-column: 2/3;
  position: relative;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  width: 1.5rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  background: var(--button-bg);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--button);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  gap: 1.4375rem;
  justify-items: start;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count, .card-grid .card__change {
  align-self: end;
}
.card-grid img, .card-grid .card__change {
  justify-self: end;
}